import React from 'react';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

import { FEATURES } from 'app/constants';
import { useIsFeatureActivated } from 'app/hooks';
import PrivateRoute from 'app/routes/PrivateRoute';
import PublicRoute from 'app/routes/PublicRoute';
import ROUTES from 'app/routes';

import LoginPage from 'pages/login';
import DashboardPage from 'pages/dashboard';
import MaintenancePage from 'pages/maintenance';
import WorksystemsPage from 'pages/worksystems';
import SpottingPage from 'pages/worksystems/spotting';

const App = () => {
  const loginFeature = useIsFeatureActivated(FEATURES.LOGIN);
  return (
    <Router>
      <Switch>
        {loginFeature && <PrivateRoute exact path={ROUTES.DASHBOARD} component={DashboardPage} />}
        {loginFeature && <PublicRoute exact path={ROUTES.LOGIN} component={LoginPage} restricted />}
        <PrivateRoute exact path={ROUTES.WORKSYSTEMS.SPOTTING} component={SpottingPage} />
        <PrivateRoute path={ROUTES.WORKSYSTEMS.INDEX} component={WorksystemsPage} />
        <Route component={MaintenancePage} />
      </Switch>
    </Router>
  );
};

export default App;
